import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Box,
  Typography,
  IconButton,
  Alert,
  CircularProgress,
  Grid,
} from '@mui/material';
import {
  Close as CloseIcon,
  CloudUpload as CloudUploadIcon,
  Delete as DeleteIcon,
} from '@mui/icons-material';
import axios from 'axios';

interface EventEditFormProps {
  open: boolean;
  onClose: () => void;
  event: {
    id: number;
    title: string;
    description: string;
    event_date: string;
    start_time: string;
    end_time: string;
    location: string;
    cover_image?: string;
  };
  onSuccess: () => void;
}

const EventEditForm: React.FC<EventEditFormProps> = ({
  open,
  onClose,
  event,
  onSuccess
}) => {
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    event_date: '',
    start_time: '',
    end_time: '',
    location: '',
  });
  const [coverImage, setCoverImage] = useState<File | null>(null);
  const [coverImageUrl, setCoverImageUrl] = useState<string>('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (event) {
      setFormData({
        title: event.title,
        description: event.description,
        event_date: event.event_date,
        start_time: event.start_time,
        end_time: event.end_time,
        location: event.location,
      });
      setCoverImageUrl(event.cover_image || '');
    }
  }, [event]);

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleImageSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      // 5MB
      if (file.size > 5 * 1024 * 1024) {
        setError('5MB');
        return;
      }

      // 
      if (!file.type.startsWith('image/')) {
        setError('');
        return;
      }

      setCoverImage(file);
      setError(null);
    }
  };

  const handleRemoveImage = () => {
    setCoverImage(null);
    setCoverImageUrl('');
  };

  const handleSubmit = async () => {
    try {
      setIsSubmitting(true);
      setError(null);

      let finalCoverImageUrl = coverImageUrl;

      // 
      if (coverImage) {
        // base64
        const reader = new FileReader();
        const base64Promise = new Promise<string>((resolve) => {
          reader.onload = () => {
            const result = reader.result as string;
            resolve(result);
          };
        });
        reader.readAsDataURL(coverImage);
        
        const imageData = await base64Promise;

        const uploadResponse = await axios.post('/api/events/upload/cover', {
          imageData: imageData,
          fileName: coverImage.name
        });

        finalCoverImageUrl = uploadResponse.data.imageUrl;
      }

      // 
      const updateData = {
        ...formData,
        cover_image: finalCoverImageUrl,
      };

      await axios.put(`/api/events/${event.id}`, updateData);

      onSuccess();
      onClose();
    } catch (error: any) {
      console.error(':', error);
      setError('');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onClose={onClose} maxWidth="md" fullWidth>
      <DialogTitle>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Typography variant="h6"></Typography>
          <IconButton onClick={onClose}>
            <CloseIcon />
          </IconButton>
        </Box>
      </DialogTitle>
      
      <DialogContent>
        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}

        <Grid container spacing={3}>
          {/*  */}
          <Grid item xs={12}>
            <Typography variant="subtitle1" sx={{ mb: 2 }}>
              
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              {(coverImageUrl || coverImage) && (
                                 <Box
                   sx={{
                     width: 100,
                     height: 60,
                     backgroundImage: coverImageUrl 
                       ? (coverImageUrl.startsWith('data:') 
                           ? `url(${coverImageUrl})` 
                           : `url(https://language-community-backend.onrender.com${coverImageUrl})`)
                       : coverImage 
                         ? `url(${URL.createObjectURL(coverImage)})` 
                         : 'none',
                     backgroundSize: 'cover',
                     backgroundPosition: 'center',
                     borderRadius: 1,
                     border: '1px solid #ddd',
                   }}
                 />
              )}
              <Button
                variant="outlined"
                component="label"
                startIcon={<CloudUploadIcon />}
                disabled={isSubmitting}
              >
                
                <input
                  type="file"
                  hidden
                  accept="image/*"
                  onChange={handleImageSelect}
                />
              </Button>
              {(coverImageUrl || coverImage) && (
                <IconButton onClick={handleRemoveImage} color="error">
                  <DeleteIcon />
                </IconButton>
              )}
            </Box>
          </Grid>

          {/*  */}
          <Grid item xs={12}>
            <TextField
              fullWidth
              label=" *"
              value={formData.title}
              onChange={(e) => handleInputChange('title', e.target.value)}
              disabled={isSubmitting}
            />
          </Grid>

          {/*  */}
          <Grid item xs={12}>
            <TextField
              fullWidth
              label=""
              multiline
              rows={3}
              value={formData.description}
              onChange={(e) => handleInputChange('description', e.target.value)}
              disabled={isSubmitting}
            />
          </Grid>

          {/*  */}
          <Grid item xs={12} md={6}>
            <TextField
              fullWidth
              label=" *"
              type="date"
              value={formData.event_date}
              onChange={(e) => handleInputChange('event_date', e.target.value)}
              disabled={isSubmitting}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>

          {/*  */}
          <Grid item xs={12} md={6}>
            <TextField
              fullWidth
              label=""
              value={formData.location}
              onChange={(e) => handleInputChange('location', e.target.value)}
              disabled={isSubmitting}
            />
          </Grid>

          {/*  */}
          <Grid item xs={12} md={6}>
            <TextField
              fullWidth
              label=""
              type="time"
              value={formData.start_time}
              onChange={(e) => handleInputChange('start_time', e.target.value)}
              disabled={isSubmitting}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>

          {/*  */}
          <Grid item xs={12} md={6}>
            <TextField
              fullWidth
              label=""
              type="time"
              value={formData.end_time}
              onChange={(e) => handleInputChange('end_time', e.target.value)}
              disabled={isSubmitting}
              InputLabelProps={{ shrink: true }}
            />
          </Grid>
        </Grid>
      </DialogContent>

      <DialogActions>
        <Button onClick={onClose} disabled={isSubmitting}>
          
        </Button>
        <Button 
          onClick={handleSubmit} 
          variant="contained" 
          disabled={isSubmitting || !formData.title || !formData.event_date}
        >
          {isSubmitting ? <CircularProgress size={20} /> : ''}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default EventEditForm; 