import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Card,
  CardContent,
  Button,
  TextField,
  Slider,
  FormControlLabel,
  Checkbox,
  FormGroup,
  Rating,
  Alert,
  CircularProgress,
  Paper,
  Divider,
} from '@mui/material';
import {
  Star as StarIcon,
  Send as SendIcon,
  CheckCircle as CheckCircleIcon,
} from '@mui/icons-material';

import { useTranslation } from 'react-i18next';
import { useTheme } from '../contexts/ThemeContext';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';

interface SurveyData {
  member_number: string;
  satisfaction_rating: number;
  recommendation_score: number;
  instructor_feedback: string;
  lesson_feedback: string;
  next_month_goals: string[];
  other_comments: string;
  completed: boolean;
  submitted_at?: string;
}

const SurveyPage: React.FC = () => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const { isDarkMode } = useTheme();
  const { month } = useParams<{ month: string }>();

  const [surveyData, setSurveyData] = useState<SurveyData>({
    member_number: '',
    satisfaction_rating: 0,
    recommendation_score: 5,
    instructor_feedback: '',
    lesson_feedback: '',
    next_month_goals: [],
    other_comments: '',
    completed: false,
  });
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [currentMonth, setCurrentMonth] = useState<string>('');
  const [isSubmitted, setIsSubmitted] = useState(false);



  // 
  const nextMonthGoals = [
    t('conversationSkills'),
    t('grammarAccuracy'),
    t('vocabulary'),
    t('pronunciation'),
    t('listening'),
    t('examPreparation'),
    t('culture'),
  ];

  useEffect(() => {
    // URL
    if (month) {
      setCurrentMonth(month);
      fetchSurveyDataForMonth(month);
    } else {
      // 
      navigate('/');
    }
  }, [navigate, month]);



  const fetchSurveyDataForMonth = async (month: string) => {
    try {
      setLoading(true);
      
      const response = await axios.get(
        `${process.env.REACT_APP_API_URL}/api/survey/month/${month}`
      );

      if (response.data.success) {
        if (response.data.survey) {
          setSurveyData(response.data.survey);
          setIsSubmitted(true);
        } else {
          // 
          setSurveyData(prev => ({ 
            ...prev, 
            completed: false 
          }));
          setIsSubmitted(false);
        }
      }
    } catch (error) {
      console.error(':', error);
      // 
      setSurveyData(prev => ({ 
        ...prev, 
        completed: false 
      }));
      setIsSubmitted(false);
    } finally {
      setLoading(false);
    }
  };

  const handleSatisfactionChange = (event: React.SyntheticEvent, value: number | null) => {
    setSurveyData(prev => ({ ...prev, satisfaction_rating: value || 0 }));
  };

  const handleRecommendationChange = (event: Event, newValue: number | number[]) => {
    setSurveyData(prev => ({ ...prev, recommendation_score: newValue as number }));
  };

  const handleGoalChange = (goal: string) => {
    setSurveyData(prev => ({
      ...prev,
      next_month_goals: prev.next_month_goals.includes(goal)
        ? prev.next_month_goals.filter(g => g !== goal)
        : [...prev.next_month_goals, goal]
    }));
  };

  const handleSubmit = async () => {
    try {
      setSubmitting(true);
      setError(null);
      setSuccess(null);

      // 
      if (!surveyData.member_number.trim()) {
        setError('');
        return;
      }

      if (surveyData.satisfaction_rating === 0) {
        setError('');
        return;
      }

      let response;
      
      // URLAPI
      response = await axios.post(
        `${process.env.REACT_APP_API_URL}/api/survey/month/${month}`,
        {
          ...surveyData,
          completed: true
        }
      );

      if (response.data.success) {
        setSuccess(t('surveySubmitted'));
        setSurveyData(prev => ({ ...prev, completed: true, submitted_at: new Date().toISOString() }));
        
        // 
        const submittedKey = `survey_submitted_${currentMonth}`;
        localStorage.setItem(submittedKey, 'true');
        setIsSubmitted(true);
      }
    } catch (error: any) {
      console.error(':', error);
      setError(error.response?.data?.message || t('surveyError'));
    } finally {
      setSubmitting(false);
    }
  };

  const formatMonthDisplay = (monthStr: string) => {
    const [year, month] = monthStr.split('-');
    return `${year}${month}`;
  };

  // 
  if (!month) {
    return null;
  }

  if (loading) {
    return (
      <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '400px' }}>
          <CircularProgress />
        </Box>
      </Container>
    );
  }

  if (surveyData.completed || isSubmitted) {
    return (
      <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
        <Card>
          <CardContent sx={{ textAlign: 'center', py: 6 }}>
            <CheckCircleIcon sx={{ fontSize: 64, color: 'success.main', mb: 2 }} />
            <Typography variant="h5" sx={{ mb: 2, fontWeight: 600 }}>
              {t('surveyCompleted', { month: formatMonthDisplay(currentMonth) })}
            </Typography>
            <Typography variant="body1" color="text.secondary" sx={{ mb: 3 }}>
              {t('surveyCompletedDescription')}
            </Typography>
            {surveyData.submitted_at && (
              <Typography variant="caption" color="text.secondary">
                {t('submissionTime')}: {new Date(surveyData.submitted_at).toLocaleString('ja-JP')}
              </Typography>
            )}
          </CardContent>
        </Card>
      </Container>
    );
  }

  return (
    <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
      {/*  */}
      <Box sx={{ textAlign: 'center', mb: 4 }}>
        <Typography variant="h4" component="h1" sx={{ fontWeight: 600, mb: 1 }}>
          {formatMonthDisplay(currentMonth)} | {t('feedback')}
        </Typography>
        <Typography variant="body1" color="text.secondary">
          
        </Typography>
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 3 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 3 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card>
        <CardContent sx={{ p: { xs: 2, sm: 4 } }}>
          {/*  */}
          <Box sx={{ mb: 4 }}>
            <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}>
              {t('memberNumber')}*
            </Typography>
            <TextField
              fullWidth
              value={surveyData.member_number}
              onChange={(e) => setSurveyData(prev => ({ ...prev, member_number: e.target.value }))}
              placeholder={t('memberNumberPlaceholder')}
              variant="outlined"
              sx={{ mb: 1 }}
            />
            <Typography variant="caption" color="text.secondary">
              {month ? '' : t('memberNumberHelp')}
            </Typography>
          </Box>

          {/*  */}
          <Box sx={{ mb: 4 }}>
            <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}>
              {t('satisfactionRating')}
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 1 }}>
              <Rating
                name="satisfaction"
                value={surveyData.satisfaction_rating}
                onChange={handleSatisfactionChange}
                size="large"
                sx={{
                  '& .MuiRating-iconFilled': {
                    color: 'warning.main',
                    animation: 'glow 2s ease-in-out infinite alternate, rotate 0.6s ease-in-out',
                    '@keyframes glow': {
                      '0%': {
                        filter: 'drop-shadow(0 0 5px #ff9800)',
                      },
                      '100%': {
                        filter: 'drop-shadow(0 0 20px #ff9800) drop-shadow(0 0 30px #ff9800)',
                      },
                    },
                    '@keyframes rotate': {
                      '0%': { transform: 'rotate(0deg)' },
                      '100%': { transform: 'rotate(360deg)' },
                    },
                  },
                  '& .MuiRating-iconHover': {
                    transform: 'scale(1.2)',
                    transition: 'transform 0.2s ease-in-out',
                  },
                }}
              />
              <Typography variant="body2" color="text.secondary">
                {surveyData.satisfaction_rating}/5
              </Typography>
            </Box>
            <Typography variant="caption" color="text.secondary">
              {t('satisfactionDescription')}
            </Typography>
          </Box>

          <Divider sx={{ my: 3 }} />

          {/*  */}
          <Box sx={{ mb: 4 }}>
            <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}>
              {t('recommendationQuestion')}
            </Typography>
            <Box sx={{ mb: 1 }}>
              {/*  */}
              <Slider
                value={surveyData.recommendation_score}
                onChange={handleRecommendationChange}
                min={0}
                max={10}
                step={1}
                marks
                valueLabelDisplay="auto"
                sx={{ 
                  width: '100%',
                  '& .MuiSlider-track': {
                    background: 'linear-gradient(90deg, #f44336 0%, #ff9800 20%, #ffeb3b 40%, #8bc34a 60%, #4caf50 80%, #2e7d32 100%)',
                  height: 8,
                  borderRadius: 4,
                  border: 'none',
                  boxShadow: '0 2px 4px rgba(0,0,0,0.2)',
                },
                '& .MuiSlider-thumb': {
                  width: 24,
                  height: 24,
                  backgroundColor: '#fff',
                  border: '3px solid #1976d2',
                  boxShadow: '0 4px 8px rgba(0,0,0,0.3)',
                  '&:hover': {
                    boxShadow: '0 6px 12px rgba(0,0,0,0.4)',
                    transform: 'scale(1.1)',
                  },
                  transition: 'all 0.2s ease-in-out',
                },
                '& .MuiSlider-mark': {
                  backgroundColor: '#bfbfbf',
                  height: 8,
                  width: 1,
                  '&.MuiSlider-markActive': {
                    backgroundColor: 'currentColor',
                  },
                },
              }}
            />
            
            {/*  */}
              <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
                <Box
                  sx={{
                    minWidth: 60,
                    width: 60,
                    height: 40,
                    backgroundColor: (() => {
                      const score = surveyData.recommendation_score;
                      if (score <= 1) return '#f44336'; // 
                      if (score <= 3) return '#ff9800'; // 
                      if (score <= 5) return '#ffeb3b'; // 
                      if (score <= 7) return '#8bc34a'; // 
                      if (score <= 9) return '#4caf50'; // 
                      return '#2e7d32'; // 
                    })(),
                    color: 'white',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    borderRadius: 2,
                    fontWeight: 700,
                    fontSize: '1.2rem',
                    boxShadow: '0 4px 8px rgba(0,0,0,0.2)',
                    transition: 'all 0.3s ease-in-out',
                    '&:hover': {
                      transform: 'scale(1.05)',
                      boxShadow: '0 6px 12px rgba(0,0,0,0.3)',
                    },
                  }}
                >
                  {surveyData.recommendation_score}
                </Box>
              </Box>
            </Box>
            <Typography variant="caption" color="text.secondary">
              {t('recommendationDescription')}
            </Typography>
          </Box>

          <Divider sx={{ my: 3 }} />

          {/*  */}
          <Box sx={{ mb: 4 }}>
            <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}>
              {t('instructorFeedback')}
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
              {t('instructorFeedbackExample')}
            </Typography>
            <TextField
              fullWidth
              multiline
              rows={4}
              value={surveyData.instructor_feedback}
              onChange={(e) => setSurveyData(prev => ({ ...prev, instructor_feedback: e.target.value }))}
              placeholder={t('instructorFeedbackPlaceholder')}
              variant="outlined"
            />
          </Box>

          <Box sx={{ mb: 4 }}>
            <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}>
              {t('lessonFeedback')}
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
              {t('lessonFeedbackExample')}
            </Typography>
            <TextField
              fullWidth
              multiline
              rows={4}
              value={surveyData.lesson_feedback}
              onChange={(e) => setSurveyData(prev => ({ ...prev, lesson_feedback: e.target.value }))}
              placeholder={t('lessonFeedbackPlaceholder')}
              variant="outlined"
            />
          </Box>

          <Divider sx={{ my: 3 }} />

          {/*  */}
          <Box sx={{ mb: 4 }}>
            <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}>
              {t('nextMonthGoals')}
            </Typography>
            <FormGroup>
              {nextMonthGoals.map((goal) => (
                <FormControlLabel
                  key={goal}
                  control={
                    <Checkbox
                      checked={surveyData.next_month_goals.includes(goal)}
                      onChange={() => handleGoalChange(goal)}
                    />
                  }
                  label={goal}
                  sx={{ mb: 1 }}
                />
              ))}
            </FormGroup>
          </Box>

          <Divider sx={{ my: 3 }} />

          {/*  */}
          <Box sx={{ mb: 4 }}>
            <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}>
              {t('otherComments')}
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
              {t('otherCommentsExample')}
            </Typography>
            <TextField
              fullWidth
              multiline
              rows={4}
              value={surveyData.other_comments}
              onChange={(e) => setSurveyData(prev => ({ ...prev, other_comments: e.target.value }))}
              placeholder={t('otherCommentsPlaceholder')}
              variant="outlined"
            />
          </Box>

          {/*  */}
          <Box sx={{ textAlign: 'center', mt: 4 }}>
            <Button
              variant="contained"
              size="large"
              startIcon={<SendIcon />}
              onClick={handleSubmit}
              disabled={submitting || surveyData.satisfaction_rating === 0 || !surveyData.member_number}
              sx={{ 
                minWidth: 200,
                py: 1.5,
                px: 4
              }}
            >
              {submitting ? t('submitting') : t('submitSurvey')}
            </Button>
            {(surveyData.satisfaction_rating === 0 || !surveyData.member_number) && (
              <Typography variant="caption" color="error" sx={{ display: 'block', mt: 1 }}>
                {t('requiredFields')}
              </Typography>
            )}
          </Box>
        </CardContent>
      </Card>
    </Container>
  );
};

export default SurveyPage; 