import React, { useState, useEffect, useRef } from 'react';
import {
  Container,
  Typography,
  Card,
  CardContent,
  Button,
  TextField,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Grid,
  Box,
  Paper,
  IconButton,
  Tabs,
  Tab,
  CircularProgress,
  Chip,
  Tooltip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  SelectChangeEvent,
  Alert,
  Divider
} from '@mui/material';
import { 
  Add as AddIcon, 
  Delete as DeleteIcon, 
  ContentCopy as CopyIcon,
  NavigateBefore as NavigateBeforeIcon,
  NavigateNext as NavigateNextIcon,
  Star as StarIcon,
  CheckCircle as CheckCircleIcon,
  Cancel as CancelIcon,
  Group as GroupIcon,
  Assignment as AssignmentIcon,
  SupervisorAccount as SupervisorAccountIcon,
  CalendarMonth as CalendarIcon,
  Edit as EditIcon
} from '@mui/icons-material';
import axios from 'axios';
import { useAuth } from '../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';
import AdditionalLessonModal from '../components/AdditionalLessonModal';
import CalendarEditModal from '../components/CalendarEditModal';

interface Student {
  id: number;
  name: string;
  instructor_id: number;
  instructor_name: string;
  member_number: string;
  created_at: string;
  updated_at: string;
  email?: string; // 
}

interface Instructor {
  id: number;
  username: string;
  name: string;
  role: string;
}

const Class1ManagementPage: React.FC = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  
  // 
  const hasPermission = () => {
    const role = user?.role?.trim();
    return role === 'ECG' || role === 'JCG' || role === '';
  };

  // 
  const hasManagerPermission = () => {
    const role = user?.role?.trim();
    return role === '';
  };
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState(0);
  const [addStudentDialog, setAddStudentDialog] = useState(false);
  const [editingStudent, setEditingStudent] = useState<Student | null>(null);
  const [dataLoaded, setDataLoaded] = useState(false);
  
  // 
  const [currentMonth, setCurrentMonth] = useState<string>('');
  const [selectedInstructor, setSelectedInstructor] = useState<string>('all');
  const [studentMemos, setStudentMemos] = useState<{[studentId: number]: string}>({});
  
  // 
  const [additionalLessonModalOpen, setAdditionalLessonModalOpen] = useState(false);
  const [additionalLessonsData, setAdditionalLessonsData] = useState<any[]>([]);
  
  // 
  const [calendarEditModalOpen, setCalendarEditModalOpen] = useState(false);
  const [selectedCalendarDate, setSelectedCalendarDate] = useState<string>('');
  const [calendarEventsData, setCalendarEventsData] = useState<any[]>([]);

  // 
  const [selectedInstructorCalendar, setSelectedInstructorCalendar] = useState<string>('all');
  const [calendarEvents, setCalendarEvents] = useState<{[date: string]: Array<{
    studentId: number;
    studentName: string;
    type: 'scheduled' | 'completed';
    date: string;
    source?: 'regular' | 'additional';
  }>}>({});
  
  // 
  const [popupOpen, setPopupOpen] = useState(false);
  const [selectedDate, setSelectedDate] = useState<string>('');
  const [selectedDateEvents, setSelectedDateEvents] = useState<Array<{
    studentId: number;
    studentName: string;
    type: 'scheduled' | 'completed';
    date: string;
  }>>([]);

  // 
  const [students, setStudents] = useState<Student[]>([]);
  const [instructors, setInstructors] = useState<Instructor[]>([]);
  
  // 
  const [newStudentName, setNewStudentName] = useState('');
  const [selectedNewInstructor, setSelectedNewInstructor] = useState('');
  const [newStudentMemo, setNewStudentMemo] = useState('');
  const [isNewStudent, setIsNewStudent] = useState(true);
  const [class1Members, setClass1Members] = useState<any[]>([]);

  // state
  const [temporaryMemos, setTemporaryMemos] = useState<{ [studentId: number]: string }>({});
  const [savingMemos, setSavingMemos] = useState<{ [studentId: number]: boolean }>({});

  // 
  const getCurrentMonth = () => {
    const now = new Date();
    const year = now.getFullYear();
    const month = now.getMonth() + 1;
    return `${year}-${String(month).padStart(2, '0')}`;
  };

  // 
  const getMonthRange = (monthString: string) => {
    const [year, month] = monthString.split('-').map(Number);
    return {
      start: `${year}-${String(month).padStart(2, '0')}-01`,
      end: `${year}-${String(month).padStart(2, '0')}-${new Date(year, month, 0).getDate()}`
    };
  };

  // 
  const initializeStudentMemos = async () => {
    console.log('===  ===');
    console.log(':', students.length);
    
    if (students.length === 0) {
      console.log('');
      return;
    }

    const newStudentMemos: { [studentId: number]: string } = {};
    const newTemporaryMemos: { [studentId: number]: string } = {};
    
    for (const student of students) {
      try {
        console.log(` ${student.name} (ID: ${student.id}) `);
        const memo = await getStudentMemo(student.id);
        newStudentMemos[student.id] = memo;
        newTemporaryMemos[student.id] = memo; // 
        console.log(` ${student.name} : "${memo}"`);
      } catch (error) {
        console.error(` ${student.name} :`, error);
        newStudentMemos[student.id] = '';
        newTemporaryMemos[student.id] = '';
      }
    }
    
    console.log(':', newStudentMemos);
    setStudentMemos(newStudentMemos);
    setTemporaryMemos(newTemporaryMemos);
    console.log('===  ===');
  };

  // 
  const getStudentMemo = async (studentId: number): Promise<string> => {
    try {
      console.log('===  ===');
      console.log('ID:', studentId);
      
      const token = localStorage.getItem('token');
      console.log('Token:', !!token);
      
      const response = await axios.get(`/api/student-memos/${studentId}`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      console.log('API:', response.data);
      const memo = response.data.data?.memo || '';
      console.log(`ID ${studentId} : "${memo}"`);
      console.log('===  ===');
      
      return memo;
    } catch (error: any) {
      console.error('===  ===');
      console.error('ID:', studentId);
      console.error(':', error);
      console.error(':', error.response?.data);
      console.error(':', error.response?.status);
      return '';
    }
  };

  // 
  const saveStudentMemo = async (studentId: number, memo: string) => {
    try {
      console.log(':', { studentId, memo });
      
      const token = localStorage.getItem('token');
      const response = await axios.post(`/api/student-memos/${studentId}`, {
        memo: memo
      }, {
        headers: { Authorization: `Bearer ${token}` }
      });
      console.log(':', response.data);
      return true;
    } catch (error: any) {
      console.error(':', error);
      return false;
    }
  };

  // URL
  const generateSurveyUrl = () => {
    const baseUrl = window.location.origin + window.location.pathname;
    const month = currentMonth;
    return `${baseUrl}#/survey/${month}/`;
  };

  // URL
  const copySurveyUrl = () => {
    const surveyUrl = generateSurveyUrl();
    navigator.clipboard.writeText(surveyUrl).then(() => {
      alert(`URL:\n${surveyUrl}`);
    });
  };

  // 
  const fetchAdditionalLessons = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await axios.get('/api/additional-lessons', {
        headers: { Authorization: `Bearer ${token}` }
      });
      setAdditionalLessonsData(response.data.data || []);
    } catch (error) {
      console.error(':', error);
      setAdditionalLessonsData([]);
    }
  };

  // 
  const fetchCalendarEvents = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await axios.get('/api/calendar-events', {
        headers: { Authorization: `Bearer ${token}` }
      });
      setCalendarEventsData(response.data.data || []);
    } catch (error) {
      console.error(':', error);
      setCalendarEventsData([]);
    }
  };

  // 
  const handleCalendarDateClick = (date: string) => {
    setSelectedCalendarDate(date);
    setCalendarEditModalOpen(true);
  };

  // 
  const generateCalendarEvents = () => {
    const events: {[date: string]: Array<{
      studentId: number;
      studentName: string;
      type: 'scheduled' | 'completed';
      date: string;
      source?: 'regular' | 'additional';
    }>} = {};

    // 
    calendarEventsData.forEach((event: any) => {
      // 
      if (selectedInstructorCalendar !== 'all') {
        const student = students.find(s => s.id === event.student_id);
        if (student && student.instructor_id.toString() !== selectedInstructorCalendar) {
          return;
        }
      }

      const dateKey = event.date;
      if (!events[dateKey]) events[dateKey] = [];
      
      events[dateKey].push({
        studentId: event.student_id,
        studentName: event.student_name,
        type: event.type,
        date: dateKey
      });
    });

    return events;
  };

  // 
  const updateCalendarEvents = () => {
    try {
      const events = generateCalendarEvents();
      setCalendarEvents(events);
    } catch (error) {
      console.error(':', error);
    }
  };

  // 
  const getEventsForDate = (date: string) => {
    return calendarEvents[date] || [];
  };

  // 
  const fetchData = async () => {
    try {
      setLoading(true);
      setError(null);
      const token = localStorage.getItem('token');
      
      console.log('===  ===');
      console.log('Token:', !!token);
      
      // 
      console.log('...');
      const studentsResponse = await axios.get('/api/class1/students', {
        headers: { Authorization: `Bearer ${token}` }
      });
      console.log(':', studentsResponse.data);
      const studentsData = studentsResponse.data.students || [];
      console.log(':', studentsData);
      setStudents(studentsData);

      // 
      console.log('...');
      const instructorsResponse = await axios.get('/api/auth/users/class1', {
        headers: { Authorization: `Bearer ${token}` }
      });
      console.log(':', instructorsResponse.data);
      const allUsers = instructorsResponse.data.users || [];
      console.log(':', allUsers.length);
      console.log(':', allUsers.map((u: any) => ({ name: u.name, role: u.role })));
      
      const instructorUsers = allUsers.filter((user: any) => {
        const role = user.role?.trim();
        const isInstructor = role === 'ECG' || role === 'JCG' || role === '';
        console.log(` ${user.name} (${role}): ${isInstructor ? '' : ''}`);
        return isInstructor;
      }).map((user: any) => ({
        id: user.id,
        name: user.name || user.username,
        username: user.username,
        role: user.role,
        email: user.email
      }));
      console.log(':', instructorUsers.length);
      console.log(':', instructorUsers.map((i: any) => ({ id: i.id, name: i.name, role: i.role })));
      setInstructors(instructorUsers);
      console.log('instructors:', instructorUsers);
      
      // UI
      setTimeout(() => {
        console.log('');
        setInstructors([...instructorUsers]);
      }, 100);

      // Class1 Members
      console.log('Class1 Members...');
      const class1MembersUsers = allUsers.filter((user: any) => {
        const role = user.role?.trim();
        return role === 'Class1 Members';
      });
      setClass1Members(class1MembersUsers);
      console.log('Class1 Members:', class1MembersUsers);

      setDataLoaded(true);
      console.log('===  ===');
    } catch (error: any) {
      console.error(':', error);
      console.error(':', {
        message: error.message,
        status: error.response?.status,
        data: error.response?.data,
        url: error.config?.url
      });
      setError(`: ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  // students
  useEffect(() => {
    if (students.length > 0) {
      console.log('');
      console.log(':', students.map(s => ({ id: s.id, name: s.name })));
      initializeStudentMemos();
    }
  }, [students]);

  // 
  useEffect(() => {
    const initializeData = async () => {
      if (!hasPermission()) {
        console.log('');
        setLoading(false);
        return;
      }
      
      console.log('===  ===');
      
      // 
      const currentMonthString = getCurrentMonth();
      console.log(':', currentMonthString);
      setCurrentMonth(currentMonthString);
      
      // 
      await fetchData();
      
      console.log('===  ===');
    };
    
    initializeData();
  }, []);

  // 
  useEffect(() => {
    if (currentMonth && students.length > 0) {
      console.log(':', currentMonth);
      initializeStudentMemos();
    }
  }, [currentMonth]);

  // 
  useEffect(() => {
    if (students.length > 0) {
      fetchAdditionalLessons();
      fetchCalendarEvents();
    }
  }, [students]);

  // 
  useEffect(() => {
    if (students.length > 0) {
      updateCalendarEvents();
    }
  }, [calendarEventsData, selectedInstructorCalendar, students]);

  // 
  const handleStudentMemoChange = (studentId: number, memo: string) => {
    console.log(`: ID ${studentId}, : "${memo}"`);
    setTemporaryMemos(prev => ({
      ...prev,
      [studentId]: memo
    }));
  };

  // 
  const handleSaveStudentMemo = async (studentId: number) => {
    try {
      console.log('===  ===');
      const memo = temporaryMemos[studentId] || '';
      console.log(':', { studentId, memo });
      
      setSavingMemos(prev => ({ ...prev, [studentId]: true }));
      
      const numericStudentId = Number(studentId);
      if (isNaN(numericStudentId)) {
        console.error('ID:', studentId);
        return;
      }
      
      const success = await saveStudentMemo(numericStudentId, memo);
      if (success) {
        // 
        setStudentMemos(prev => ({
          ...prev,
          [numericStudentId]: memo
        }));
        console.log(' ');
        console.log(':', memo);
        console.log('studentMemos:', studentMemos);
        
        // 
        alert('');
      } else {
        console.error(' ');
        alert('');
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    } finally {
      setSavingMemos(prev => ({ ...prev, [studentId]: false }));
    }
  };

  // 
  const handleDeleteStudent = async (studentId: number) => {
    if (!window.confirm('')) return;

    try {
      const token = localStorage.getItem('token');
      const response = await axios.delete(`/api/class1/students/${studentId}`, {
        headers: { Authorization: `Bearer ${token}` }
      });

      if (response.data.success) {
        fetchData();
        alert('');
      }
    } catch (error: any) {
      console.error(':', error);
      alert('');
    }
  };

  // 
  const handleAddStudent = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await axios.post('/api/class1/students', {
        name: newStudentName,
        instructor_id: parseInt(selectedNewInstructor),
        memo: newStudentMemo
      }, {
        headers: { Authorization: `Bearer ${token}` }
      });

      if (response.data.success) {
        setAddStudentDialog(false);
        setNewStudentName('');
        setSelectedNewInstructor('');
        setNewStudentMemo('');
        setIsNewStudent(true);
        fetchData();
        alert('');
      }
    } catch (error: any) {
      console.error(':', error);
      alert('');
    }
  };

  // 
  const handlePreviousMonth = () => {
    console.log(' - :', currentMonth);
    const [year, month] = currentMonth.split('-');
    const yearNum = parseInt(year);
    const monthNum = parseInt(month);
    
    let newYear = yearNum;
    let newMonth = monthNum - 1;
    
    if (newMonth === 0) {
      newYear = yearNum - 1;
      newMonth = 12;
    }
    
    const newMonthString = `${newYear}-${newMonth.toString().padStart(2, '0')}`;
    console.log(':', newMonthString);
    setCurrentMonth(newMonthString);
    
    // 
    setTimeout(() => {
      initializeStudentMemos();
      if (activeTab === 1) {
        fetchCalendarEvents();
      }
    }, 100);
  };

  const handleNextMonth = () => {
    console.log(' - :', currentMonth);
    const [year, month] = currentMonth.split('-');
    const yearNum = parseInt(year);
    const monthNum = parseInt(month);
    
    let newYear = yearNum;
    let newMonth = monthNum + 1;
    
    if (newMonth === 13) {
      newYear = yearNum + 1;
      newMonth = 1;
    }
    
    const newMonthString = `${newYear}-${newMonth.toString().padStart(2, '0')}`;
    console.log(':', newMonthString);
    setCurrentMonth(newMonthString);
    
    // 
    setTimeout(() => {
      initializeStudentMemos();
      if (activeTab === 1) {
        fetchCalendarEvents();
      }
    }, 100);
  };

  // 
  const filteredStudents = students.filter(student => {
    if (selectedInstructor === 'all') return true;
    return student.instructor_id.toString() === selectedInstructor;
  });

  // 
  const getInstructorName = (instructorId: number) => {
    console.log(` - ID: ${instructorId}, : ${instructors.length}`);
    console.log(`:`, instructors);
    const instructor = instructors.find(i => i.id === instructorId);
    console.log(`:`, instructor);
    if (!instructor) {
      console.warn(`ID ${instructorId} `);
    }
    return instructor ? instructor.name : '';
  };

  // 
  const [weekStartsOnMonday, setWeekStartsOnMonday] = useState<boolean>(true);

  // 
  const getWeekDays = () => {
    if (weekStartsOnMonday) {
      return ['', '', '', '', '', '', ''];
    } else {
      return ['', '', '', '', '', '', ''];
    }
  };

  // 0=, 1=, ...
  const getDayIndex = (date: Date) => {
    const day = date.getDay(); // 0=, 1=, ...
    if (weekStartsOnMonday) {
      // : 0=, 1=, ..., 6=
      return day === 0 ? 6 : day - 1;
    } else {
      // : 0=, 1=, ..., 6=
      return day;
    }
  };

  if (loading) {
    return (
      <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
        <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
          <CircularProgress />
        </Box>
      </Container>
    );
  }

  if (error) {
    return (
      <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
        <Alert severity="error">{error}</Alert>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Typography variant="h4" component="h1">
          Class1 
        </Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={() => setAddStudentDialog(true)}
        >
          
        </Button>
      </Box>

      {/*  */}
      <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
        <Tabs value={activeTab} onChange={(e, newValue) => setActiveTab(newValue)}>
          <Tab label="" />
          <Tab label="" />
          {hasManagerPermission() && (
            <Tab label="" />
          )}
        </Tabs>
      </Box>

      {/*  */}
      {activeTab === 0 && (
        <Box>
          {/*  */}
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
            <Typography variant="h5" sx={{ fontWeight: 600 }}>
              
            </Typography>
            <Box sx={{ display: 'flex', gap: 2 }}>
              <Tooltip title="URL">
                <Button
                  variant="outlined"
                  startIcon={<CopyIcon />}
                  onClick={() => copySurveyUrl()}
                  size="small"
                >
                  URL
                </Button>
              </Tooltip>
            </Box>
          </Box>

          <Grid container spacing={3}>
            {filteredStudents.map((student) => (
              <Grid item xs={12} md={6} key={student.id}>
                <Card>
                  <CardContent>
                    <Box display="flex" justifyContent="space-between" alignItems="flex-start" mb={2}>
                      <Box>
                        <Typography variant="h6" gutterBottom>
                          {student.name}
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                          : {getInstructorName(student.instructor_id)} : {student.member_number}
                        </Typography>
                      </Box>
                      <Box display="flex" gap={1}>
                        <Tooltip title="">
                          <IconButton
                            size="small"
                            color="error"
                            onClick={() => handleDeleteStudent(student.id)}
                          >
                            <DeleteIcon />
                          </IconButton>
                        </Tooltip>
                      </Box>
                    </Box>
                  
                    
                    <Divider sx={{ my: 2 }} />
                    
                    {/*  */}
                    <Box>
                      <Typography variant="subtitle2" gutterBottom>
                        <AssignmentIcon sx={{ fontSize: 16, mr: 0.5 }} />
                        
                      </Typography>
                      <TextField
                        fullWidth
                        multiline
                        rows={3}
                        variant="outlined"
                        size="small"
                        placeholder="..."
                        value={temporaryMemos[student.id] || ''}
                        onChange={(e) => handleStudentMemoChange(student.id, e.target.value)}
                      />
                      <Button
                        variant="outlined"
                        size="small"
                        onClick={() => handleSaveStudentMemo(student.id)}
                        disabled={savingMemos[student.id] || !temporaryMemos[student.id]}
                        sx={{ mt: 1 }}
                      >
                        {savingMemos[student.id] ? <CircularProgress size={20} /> : ''}
                      </Button>
                    </Box>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Box>
      )}

      {/*  */}
      {activeTab === 1 && (
        <Box>
          <Typography variant="h5" sx={{ fontWeight: 600, mb: 3 }}>
            
          </Typography>

          {/*  */}
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 3 }}>
            <Typography variant="body1" sx={{ fontWeight: 500 }}>
              :
            </Typography>
            <FormControl sx={{ minWidth: 200 }}>
              <Select
                value={selectedInstructorCalendar}
                onChange={(e) => setSelectedInstructorCalendar(e.target.value)}
                size="small"
              >
                <MenuItem value="all"></MenuItem>
                {instructors.length === 0 ? (
                  <MenuItem disabled value="">
                    ...
                  </MenuItem>
                ) : (
                  instructors.map((instructor) => (
                    <MenuItem key={instructor.id} value={instructor.id.toString()}>
                      {instructor.name}
                    </MenuItem>
                  ))
                )}
              </Select>
            </FormControl>
          </Box>

          {/*  */}
          <Box sx={{ mb: 3 }}>
            {/*  */}
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', mb: 2 }}>
              <IconButton onClick={handlePreviousMonth}>
                <NavigateBeforeIcon />
              </IconButton>
              
              <Typography variant="h6" sx={{ fontWeight: 600 }}>
                {currentMonth.split('-')[0]}{parseInt(currentMonth.split('-')[1])}
              </Typography>
              
              <IconButton onClick={handleNextMonth}>
                <NavigateNextIcon />
              </IconButton>
            </Box>

            {/*  */}
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
              <Typography variant="body2" color="text.secondary">
                :
              </Typography>
              <Button
                variant={weekStartsOnMonday ? "contained" : "outlined"}
                size="small"
                onClick={() => setWeekStartsOnMonday(true)}
                sx={{ minWidth: 80 }}
              >
                
              </Button>
              <Button
                variant={!weekStartsOnMonday ? "contained" : "outlined"}
                size="small"
                onClick={() => setWeekStartsOnMonday(false)}
                sx={{ minWidth: 80 }}
              >
                
              </Button>
            </Box>

            {/*  */}
            <Box sx={{ 
              display: 'grid', 
              gridTemplateColumns: 'repeat(7, 1fr)', 
              gap: 1,
              border: '1px solid #e0e0e0',
              borderRadius: 1
            }}>
              {/*  */}
              {getWeekDays().map((day) => (
                <Box key={day} sx={{ 
                  p: 1, 
                  textAlign: 'center', 
                  fontWeight: 600,
                  backgroundColor: 'rgba(0,0,0,0.05)',
                  borderBottom: '1px solid #e0e0e0'
                }}>
                  <Typography variant="body2">{day}</Typography>
                </Box>
              ))}

              {/*  */}
              {(() => {
                const [year, month] = currentMonth.split('-');
                const yearNum = parseInt(year);
                const monthNum = parseInt(month);
                
                const firstDay = new Date(yearNum, monthNum - 1, 1);
                const lastDay = new Date(yearNum, monthNum, 0);
                const daysInMonth = lastDay.getDate();
                const firstDayOfWeek = getDayIndex(firstDay);
                
                const days = [];
                
                // 
                for (let i = 0; i < firstDayOfWeek; i++) {
                  days.push(
                    <Box key={`empty-${i}`} sx={{ 
                      p: 1, 
                      minHeight: 60,
                      backgroundColor: 'rgba(0,0,0,0.02)'
                    }} />
                  );
                }
                
                // 
                for (let day = 1; day <= daysInMonth; day++) {
                  const dateKey = `${yearNum}-${monthNum.toString().padStart(2, '0')}-${day.toString().padStart(2, '0')}`;
                  const events = getEventsForDate(dateKey);
                  const isToday = new Date().toISOString().split('T')[0] === dateKey;
                  
                  days.push(
                    <Box key={day} sx={{ 
                      p: 1, 
                      minHeight: 60,
                      border: '1px solid #e0e0e0',
                      backgroundColor: isToday ? 'rgba(25, 118, 210, 0.1)' : 'transparent',
                      position: 'relative',
                      cursor: 'pointer',
                      '&:hover': {
                        backgroundColor: 'rgba(0,0,0,0.1)'
                      }
                    }}
                    onClick={() => handleCalendarDateClick(dateKey)}
                    >
                      <Typography variant="body2" sx={{ 
                        fontWeight: isToday ? 600 : 400,
                        color: isToday ? 'primary.main' : 'inherit'
                      }}>
                        {day}
                      </Typography>
                      
                      {events.length > 0 && (
                        <Box sx={{ 
                          position: 'absolute', 
                          top: 2, 
                          right: 2,
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                          width: 20,
                          height: 20,
                          borderRadius: '50%',
                          backgroundColor: events.some(e => e.type === 'completed') ? 'success.main' : 'warning.main',
                          color: 'white',
                          fontSize: '0.75rem',
                          fontWeight: 600
                        }}>
                          {events.length > 1 ? events.length : 
                           events.some(e => e.type === 'completed') ? '' : ''}
                        </Box>
                      )}
                    </Box>
                  );
                }
                
                return days;
              })()}
            </Box>
          </Box>

          {/*  */}
          <Box sx={{ display: 'flex', gap: 3, alignItems: 'center' }}>
            <Typography variant="body2" sx={{ fontWeight: 500 }}>
              :
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <Box sx={{ 
                width: 16, 
                height: 16, 
                borderRadius: '50%', 
                backgroundColor: 'success.main',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '0.75rem',
                color: 'white'
              }}>
                
              </Box>
              <Typography variant="body2"></Typography>
            </Box>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <Box sx={{ 
                width: 16, 
                height: 16, 
                borderRadius: '50%', 
                backgroundColor: 'warning.main',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '0.75rem',
                color: 'white'
              }}>
                
              </Box>
              <Typography variant="body2"></Typography>
            </Box>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <Box sx={{ 
                width: 16, 
                height: 16, 
                borderRadius: '50%', 
                backgroundColor: 'primary.main',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '0.75rem',
                color: 'white',
                fontWeight: 600
              }}>
                3
              </Box>
              <Typography variant="body2"></Typography>
            </Box>
          </Box>
        </Box>
      )}

      {/*  */}
      <AdditionalLessonModal
        open={additionalLessonModalOpen}
        onClose={() => setAdditionalLessonModalOpen(false)}
        weekKey={currentMonth}
        onSuccess={() => {
          fetchAdditionalLessons();
        }}
      />

      {/*  */}
      <Dialog open={addStudentDialog} onClose={() => setAddStudentDialog(false)} maxWidth="sm" fullWidth>
        <DialogTitle></DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 1 }}>
            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel></InputLabel>
              <Select
                value={isNewStudent ? 'new' : 'existing'}
                onChange={(e) => {
                  setIsNewStudent(e.target.value === 'new');
                  setNewStudentName('');
                }}
                label=""
              >
                <MenuItem value="new"></MenuItem>
                <MenuItem value="existing">Class1 Members</MenuItem>
              </Select>
            </FormControl>

            {isNewStudent ? (
              <TextField
                fullWidth
                label=""
                value={newStudentName}
                onChange={(e) => setNewStudentName(e.target.value)}
                sx={{ mb: 2 }}
                placeholder=""
              />
            ) : (
              <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel>Class1 Members</InputLabel>
                <Select
                  value={newStudentName}
                  onChange={(e) => setNewStudentName(e.target.value)}
                  label="Class1 Members"
                >
                  {!dataLoaded ? (
                    <MenuItem disabled value="">
                      ...
                    </MenuItem>
                  ) : class1Members.length === 0 ? (
                    <MenuItem disabled value="">
                      Class1 Members
                    </MenuItem>
                  ) : (
                    class1Members.map((member) => (
                      <MenuItem key={member.id} value={member.username}>
                        {member.username}
                      </MenuItem>
                    ))
                  )}
                </Select>
              </FormControl>
            )}

            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel></InputLabel>
              <Select
                value={selectedNewInstructor}
                onChange={(e) => setSelectedNewInstructor(e.target.value)}
                label=""
              >
                {!dataLoaded ? (
                  <MenuItem disabled value="">
                    ...
                  </MenuItem>
                ) : instructors.length === 0 ? (
                  <MenuItem disabled value="">
                    
                  </MenuItem>
                ) : (
                                     instructors.map((instructor) => (
                     <MenuItem key={instructor.id} value={instructor.id.toString()}>
                       {instructor.name}
                     </MenuItem>
                   ))
                )}
              </Select>
            </FormControl>

            <TextField
              fullWidth
              label=""
              value={newStudentMemo}
              onChange={(e) => setNewStudentMemo(e.target.value)}
              multiline
              rows={2}
              sx={{ mb: 2 }}
              placeholder=": "
            />

            <Typography variant="caption" color="text.secondary">
              
            </Typography>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setAddStudentDialog(false)}></Button>
          <Button 
            onClick={handleAddStudent} 
            variant="contained"
            disabled={!newStudentName || !selectedNewInstructor}
          >
            
          </Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      {activeTab === 2 && hasManagerPermission() && (
        <Box sx={{ textAlign: 'center', py: 8 }}>
          <Typography variant="h5" sx={{ mb: 2 }}>
            
          </Typography>
          <Typography variant="body1" color="text.secondary" sx={{ mb: 3 }}>
            
          </Typography>
          <Button
            variant="contained"
            onClick={() => navigate('/manager')}
            startIcon={<SupervisorAccountIcon />}
          >
            
          </Button>
        </Box>
      )}

      {/*  */}
      <CalendarEditModal
        open={calendarEditModalOpen}
        onClose={() => setCalendarEditModalOpen(false)}
        date={selectedCalendarDate}
        students={students}
        events={getEventsForDate(selectedCalendarDate)}
        onSuccess={() => {
          fetchCalendarEvents();
        }}
      />
    </Container>
  );
};

export default Class1ManagementPage; 