import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  Tabs,
  Tab,
  Card,
  CardContent,
  TextField,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Chip,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Alert,
  Divider,
  Stack,
  Fade,
} from '@mui/material';
import {
  Add as AddIcon,
  Delete as DeleteIcon,
  Edit as EditIcon,
  AdminPanelSettings,
  KeyboardArrowUp,
  KeyboardArrowDown,
} from '@mui/icons-material';
import { useAuth } from '../contexts/AuthContext';
import { useCommunity } from '../contexts/CommunityContext';
import axios from 'axios';
import { useTranslation } from 'react-i18next';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`admin-tabpanel-${index}`}
      aria-labelledby={`admin-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

const AdminPanel: React.FC = () => {
  const { user } = useAuth();
  const { t } = useTranslation();
  const {
    categories,
    channels,
    loadCategories,
    loadChannels,
    createCategory,
    createChannel,
    deleteChannel,
    reorderCategories,
    reorderChannels,
  } = useCommunity();

  const [tabValue, setTabValue] = useState(0);
  const [users, setUsers] = useState<any[]>([]);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  // 
  const [newCategoryName, setNewCategoryName] = useState('');
  const [categoryDialogOpen, setCategoryDialogOpen] = useState(false);
  const [editingCategory, setEditingCategory] = useState<any>(null);
  const [editCategoryName, setEditCategoryName] = useState('');

  // 
  const [newChannelData, setNewChannelData] = useState({
    name: '',
    description: '',
    categoryId: '',
    channelType: ''
  });
  const [channelDialogOpen, setChannelDialogOpen] = useState(false);
  const [editingChannel, setEditingChannel] = useState<any>(null);
  const [editChannelData, setEditChannelData] = useState({
    name: '',
    description: '',
    channelType: ''
  });

  // 
  const [selectedUser, setSelectedUser] = useState<any>(null);
  const [newRole, setNewRole] = useState('');
  const [roleDialogOpen, setRoleDialogOpen] = useState(false);

  // 
  const [deleteUserDialogOpen, setDeleteUserDialogOpen] = useState(false);
  const [userToDelete, setUserToDelete] = useState<any>(null);

  useEffect(() => {
    if (user?.role === '') {
      loadData();
    }
  }, [user]);

  const loadData = async () => {
    try {
      await loadCategories();
      await loadUsers();
    } catch (error) {
      console.error(':', error);
    }
  };

  const loadUsers = async () => {
    try {
      const response = await axios.get('/api/auth/users');
      setUsers(response.data.users);
    } catch (error: any) {
      setError('');
    }
  };

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  const handleCreateCategory = async () => {
    if (!newCategoryName.trim()) {
      setError('');
      return;
    }

    try {
      await createCategory(newCategoryName);
      setNewCategoryName('');
      setCategoryDialogOpen(false);
      setSuccess('');
      setError('');
    } catch (error: any) {
      setError(error.response?.data?.error || '');
    }
  };

  const handleCreateChannel = async () => {
    if (!newChannelData.name.trim() || !newChannelData.categoryId || !newChannelData.channelType) {
      setError('');
      return;
    }

    try {
      await createChannel(
        Number(newChannelData.categoryId),
        newChannelData.name,
        newChannelData.description,
        newChannelData.channelType
      );
      setNewChannelData({ name: '', description: '', categoryId: '', channelType: '' });
      setChannelDialogOpen(false);
      setSuccess('');
      setError('');
    } catch (error: any) {
      setError(error.response?.data?.error || '');
    }
  };

  const handleDeleteChannel = async (channelId: number) => {
    if (window.confirm('')) {
      try {
        await deleteChannel(channelId);
        setSuccess('');
        setError('');
      } catch (error: any) {
        setError(error.response?.data?.error || '');
      }
    }
  };

  const handleChangeUserRole = async () => {
    if (!selectedUser || !newRole) return;

    try {
      await axios.put(`/api/auth/users/${selectedUser.id}/role`, { role: newRole });
      await loadUsers();
      setRoleDialogOpen(false);
      setSelectedUser(null);
      setNewRole('');
      setSuccess('');
      setError('');
    } catch (error: any) {
      setError(error.response?.data?.error || '');
    }
  };

  const handleDeleteUser = async () => {
    if (!userToDelete) return;

    try {
      await axios.delete(`/api/auth/users/${userToDelete.id}`);
      await loadUsers();
      setDeleteUserDialogOpen(false);
      setUserToDelete(null);
      setSuccess('');
      setError('');
    } catch (error: any) {
      setError(error.response?.data?.error || '');
    }
  };

  const openDeleteUserDialog = (user: any) => {
    setUserToDelete(user);
    setDeleteUserDialogOpen(true);
  };

  const openRoleDialog = (user: any) => {
    setSelectedUser(user);
    setNewRole(user.role);
    setRoleDialogOpen(true);
  };

  const handleEditCategory = async () => {
    try {
      if (!editCategoryName.trim()) {
        setError('');
        return;
      }

      const response = await axios.put(`/api/channels/categories/${editingCategory.id}`, {
        name: editCategoryName
      });

      setSuccess('');
      setEditingCategory(null);
      setEditCategoryName('');
      // 
      await loadData();
    } catch (error: any) {
      setError(error.response?.data?.error || '');
    }
  };

  const handleEditChannel = async () => {
    try {
      if (!editChannelData.name || !editChannelData.channelType) {
        setError('');
        return;
      }

      const response = await axios.put(`/api/channels/channels/${editingChannel.id}`, {
        name: editChannelData.name,
        description: editChannelData.description,
        channel_type: editChannelData.channelType
      });

      setSuccess('');
      setEditingChannel(null);
      setEditChannelData({ name: '', description: '', channelType: '' });
      // 
      await loadData();
    } catch (error: any) {
      setError(error.response?.data?.error || '');
    }
  };

  // 
  const handleDeleteCategory = async (categoryId: number) => {
    try {
      await axios.delete(`/api/channels/categories/${categoryId}`);
      setSuccess('');
      await loadData();
    } catch (error: any) {
      setError(error.response?.data?.error || '');
    }
  };

  // 
  const moveCategoryUp = async (categoryIndex: number) => {
    if (categoryIndex === 0) return; // 

    try {
      const reorderedCategories = Array.from(categories);
      const temp = reorderedCategories[categoryIndex];
      reorderedCategories[categoryIndex] = reorderedCategories[categoryIndex - 1];
      reorderedCategories[categoryIndex - 1] = temp;

      const categoryIds = reorderedCategories.map(cat => cat.id);
      console.log('ID:', categoryIds);
      
      await reorderCategories(categoryIds);
      
      setSuccess('');
      await loadData();
    } catch (error: any) {
      console.error(':', error);
      setError(error.response?.data?.error || '');
    }
  };

  // 
  const moveCategoryDown = async (categoryIndex: number) => {
    if (categoryIndex === categories.length - 1) return; // 

    try {
      const reorderedCategories = Array.from(categories);
      const temp = reorderedCategories[categoryIndex];
      reorderedCategories[categoryIndex] = reorderedCategories[categoryIndex + 1];
      reorderedCategories[categoryIndex + 1] = temp;

      const categoryIds = reorderedCategories.map(cat => cat.id);
      console.log('ID:', categoryIds);
      
      await reorderCategories(categoryIds);
      
      setSuccess('');
      await loadData();
    } catch (error: any) {
      console.error(':', error);
      setError(error.response?.data?.error || '');
    }
  };

  // 
  const moveChannelUp = async (categoryId: number, channelIndex: number) => {
    const categoryChannels = channels[categoryId] || [];
    if (channelIndex === 0) return; // 

    try {
      const reorderedChannels = Array.from(categoryChannels);
      const temp = reorderedChannels[channelIndex];
      reorderedChannels[channelIndex] = reorderedChannels[channelIndex - 1];
      reorderedChannels[channelIndex - 1] = temp;

      const channelIds = reorderedChannels.map(channel => channel.id);
      await reorderChannels(channelIds);
      
      setSuccess('');
      await loadData();
    } catch (error: any) {
      setError(error.response?.data?.error || '');
    }
  };

  // 
  const moveChannelDown = async (categoryId: number, channelIndex: number) => {
    const categoryChannels = channels[categoryId] || [];
    if (channelIndex === categoryChannels.length - 1) return; // 

    try {
      const reorderedChannels = Array.from(categoryChannels);
      const temp = reorderedChannels[channelIndex];
      reorderedChannels[channelIndex] = reorderedChannels[channelIndex + 1];
      reorderedChannels[channelIndex + 1] = temp;

      const channelIds = reorderedChannels.map(channel => channel.id);
      await reorderChannels(channelIds);
      
      setSuccess('');
      await loadData();
    } catch (error: any) {
      setError(error.response?.data?.error || '');
    }
  };

  const openEditCategoryDialog = (category: any) => {
    setEditingCategory(category);
    setEditCategoryName(category.name);
  };

  const openEditChannelDialog = (channel: any) => {
    setEditingChannel(channel);
    setEditChannelData({
      name: channel.name,
      description: channel.description || '',
      channelType: channel.channel_type
    });
  };

  const getRoleGradient = (role: string) => {
    switch (role) {
      case '':
        return 'linear-gradient(135deg, #ff6b6b 0%, #ee5a24 100%)';
      case 'ECG':
      case 'JCG':
        return 'linear-gradient(135deg, #feca57 0%, #ff9ff3 100%)';
      case 'ECG':
        return 'linear-gradient(135deg, #48cae4 0%, #0077b6 100%)';
      case 'JCG':
        return 'linear-gradient(135deg, #a8e6cf 0%, #56ab2f 100%)';
      case '':
        return 'linear-gradient(135deg, #95a5a6 0%, #7f8c8d 100%)';
      default:
        return 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)';
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ja-JP');
  };

  const channelTypeLabels = {
    'admin_only_instructors_view': '',
    'admin_only_all_view': '',
    'instructors_post_all_view': '',
    'all_post_all_view': ''
  };

  const roles = [
    '',
    'Trial',
    'ECG',
    'JCG',
    'Class1 Members',
    'ECG',
    'JCG',
    ''
  ];

  const channelTypes = [
    { value: 'all_post_all_view', label: t('channelType.all_post_all_view') },
    { value: 'admin_only_all_view', label: t('channelType.admin_only_all_view') },
    { value: 'instructors_post_all_view', label: t('channelType.instructors_post_all_view') },
    { value: 'admin_only_instructors_view', label: t('channelType.admin_only_instructors_view') },
    { value: 'class1_post_class1_view', label: t('channelType.class1_post_class1_view') }
  ];

  if (user?.role !== '') {
    return (
      <Container maxWidth="md" sx={{ py: 4 }}>
        <Alert severity="error"></Alert>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Fade in timeout={800}>
        <Box>
          {/*  */}
          <Box sx={{ mb: 4 }}>
            <Stack direction="row" spacing={2} alignItems="center" sx={{ mb: 2 }}>
              <AdminPanelSettings sx={{ fontSize: 40, color: 'primary.main' }} />
              <Typography variant="h3" component="h1" fontWeight={700}>
                
              </Typography>
            </Stack>
            <Typography variant="body1" color="text.secondary">
              
            </Typography>
          </Box>

          {/*  */}
          {error && (
            <Alert severity="error" sx={{ mb: 3 }} onClose={() => setError('')}>
              {error}
            </Alert>
          )}
          {success && (
            <Alert severity="success" sx={{ mb: 3 }} onClose={() => setSuccess('')}>
              {success}
            </Alert>
          )}

          {/*  */}
          <Card elevation={0} sx={{ border: '1px solid rgba(0, 0, 0, 0.08)' }}>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
              <Tabs value={tabValue} onChange={handleTabChange}>
                <Tab label="" />
                <Tab label="" />
                <Tab label="" />
              </Tabs>
            </Box>

            {/*  */}
            <TabPanel value={tabValue} index={0}>
              <TableContainer>
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {users.map((user) => (
                      <TableRow key={user.id}>
                        <TableCell>{user.username}</TableCell>
                        <TableCell>{user.email}</TableCell>
                        <TableCell>
                          <Chip
                            label={user.role}
                            size="small"
                            sx={{
                              background: getRoleGradient(user.role),
                              color: 'white',
                              fontWeight: 500,
                            }}
                          />
                        </TableCell>
                        <TableCell>{formatDate(user.created_at)}</TableCell>
                        <TableCell>
                          <IconButton
                            size="small"
                            onClick={() => openRoleDialog(user)}
                            color="primary"
                          >
                            <EditIcon />
                          </IconButton>
                          <IconButton
                            size="small"
                            onClick={() => openDeleteUserDialog(user)}
                            color="error"
                            disabled={user.role === ''}
                          >
                            <DeleteIcon />
                          </IconButton>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </TabPanel>

            {/*  */}
            <TabPanel value={tabValue} index={1}>
              <Box sx={{ mb: 3 }}>
                <Button
                  variant="contained"
                  startIcon={<AddIcon />}
                  onClick={() => setCategoryDialogOpen(true)}
                >
                  
                </Button>
              </Box>
              
              <Box>
                {categories.map((category, index) => (
                  <Paper
                    key={category.id}
                    elevation={1}
                    sx={{
                      mb: 2,
                      p: 2,
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'space-between',
                      border: '1px solid rgba(0, 0, 0, 0.12)',
                      borderRadius: 2,
                      transition: 'all 0.2s ease',
                      '&:hover': {
                        backgroundColor: 'rgba(30, 64, 175, 0.04)',
                      },
                    }}
                  >
                    <Box sx={{ display: 'flex', alignItems: 'center', flex: 1 }}>
                      <Box sx={{ flex: 1 }}>
                        <Typography variant="h6" fontWeight={500}>
                          {category.name}
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                          : {formatDate(category.created_at)}
                        </Typography>
                      </Box>
                    </Box>
                    <Box sx={{ display: 'flex', gap: 1 }}>
                      <IconButton
                        size="small"
                        onClick={() => openEditCategoryDialog(category)}
                        color="primary"
                      >
                        <EditIcon />
                      </IconButton>
                      <IconButton
                        size="small"
                        onClick={() => moveCategoryUp(index)}
                        color="primary"
                        disabled={index === 0}
                        title=""
                      >
                        <KeyboardArrowUp />
                      </IconButton>
                      <IconButton
                        size="small"
                        onClick={() => moveCategoryDown(index)}
                        color="primary"
                        disabled={index === categories.length - 1}
                        title=""
                      >
                        <KeyboardArrowDown />
                      </IconButton>
                      <IconButton
                        size="small"
                        onClick={() => handleDeleteCategory(category.id)}
                        color="error"
                        title=""
                      >
                        <DeleteIcon />
                      </IconButton>
                    </Box>
                  </Paper>
                ))}
              </Box>
            </TabPanel>

            {/*  */}
            <TabPanel value={tabValue} index={2}>
              <Box sx={{ mb: 3 }}>
                <Button
                  variant="contained"
                  startIcon={<AddIcon />}
                  onClick={() => setChannelDialogOpen(true)}
                >
                  
                </Button>
              </Box>

              {categories.map((category) => {
                const categoryChannels = channels[category.id] || [];
                if (!categoryChannels.length) return null;

                return (
                  <Box key={category.id} sx={{ mb: 4 }}>
                    <Typography variant="h6" gutterBottom>
                      {category.name}
                    </Typography>
                    <Box>
                      {categoryChannels.map((channel, index) => (
                        <Paper
                          key={channel.id}
                          elevation={1}
                          sx={{
                            mb: 2,
                            p: 2,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'space-between',
                            border: '1px solid rgba(0, 0, 0, 0.12)',
                            borderRadius: 2,
                            transition: 'all 0.2s ease',
                            '&:hover': {
                              backgroundColor: 'rgba(30, 64, 175, 0.04)',
                            },
                          }}
                        >
                          <Box sx={{ display: 'flex', alignItems: 'center', flex: 1 }}>
                            <Box sx={{ flex: 1 }}>
                              <Typography variant="subtitle1" fontWeight={500}>
                                {channel.name}
                              </Typography>
                              <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
                                {channel.description}
                              </Typography>
                              <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
                                <Chip
                                  label={channelTypeLabels[channel.channel_type as keyof typeof channelTypeLabels]}
                                  size="small"
                                  variant="outlined"
                                />
                                <Typography variant="caption" color="text.secondary">
                                  : {channel.post_count}
                                </Typography>
                              </Box>
                            </Box>
                          </Box>
                          <Box sx={{ display: 'flex', gap: 1 }}>
                            <IconButton
                              size="small"
                              onClick={() => openEditChannelDialog(channel)}
                              color="primary"
                            >
                              <EditIcon />
                            </IconButton>
                            <IconButton
                              size="small"
                              onClick={() => moveChannelUp(category.id, index)}
                              color="primary"
                              disabled={index === 0}
                              title=""
                            >
                              <KeyboardArrowUp />
                            </IconButton>
                            <IconButton
                              size="small"
                              onClick={() => moveChannelDown(category.id, index)}
                              color="primary"
                              disabled={index === categoryChannels.length - 1}
                              title=""
                            >
                              <KeyboardArrowDown />
                            </IconButton>
                            <IconButton
                              size="small"
                              onClick={() => handleDeleteChannel(channel.id)}
                              color="error"
                            >
                              <DeleteIcon />
                            </IconButton>
                          </Box>
                        </Paper>
                      ))}
                    </Box>
                  </Box>
                );
              })}
            </TabPanel>
          </Card>

          {/*  */}
          <Dialog open={categoryDialogOpen} onClose={() => setCategoryDialogOpen(false)}>
            <DialogTitle></DialogTitle>
            <DialogContent>
              <TextField
                autoFocus
                margin="dense"
                label=""
                fullWidth
                variant="outlined"
                value={newCategoryName}
                onChange={(e) => setNewCategoryName(e.target.value)}
              />
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setCategoryDialogOpen(false)}></Button>
              <Button onClick={handleCreateCategory} variant="contained">
                
              </Button>
            </DialogActions>
          </Dialog>

          {/*  */}
          <Dialog open={channelDialogOpen} onClose={() => setChannelDialogOpen(false)} maxWidth="sm" fullWidth>
            <DialogTitle></DialogTitle>
            <DialogContent>
              <Stack spacing={2} sx={{ mt: 1 }}>
                <TextField
                  label=""
                  fullWidth
                  variant="outlined"
                  value={newChannelData.name}
                  onChange={(e) => setNewChannelData({ ...newChannelData, name: e.target.value })}
                  required
                />
                <TextField
                  label=""
                  fullWidth
                  variant="outlined"
                  multiline
                  rows={2}
                  value={newChannelData.description}
                  onChange={(e) => setNewChannelData({ ...newChannelData, description: e.target.value })}
                />
                <FormControl fullWidth required>
                  <InputLabel></InputLabel>
                  <Select
                    value={newChannelData.categoryId}
                    onChange={(e) => setNewChannelData({ ...newChannelData, categoryId: e.target.value })}
                  >
                    {categories.map((category) => (
                      <MenuItem key={category.id} value={category.id}>
                        {category.name}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <FormControl fullWidth required>
                  <InputLabel></InputLabel>
                  <Select
                    value={newChannelData.channelType}
                    onChange={(e) => setNewChannelData({ ...newChannelData, channelType: e.target.value })}
                  >
                    {channelTypes.map((type) => (
                      <MenuItem key={type.value} value={type.value}>
                        {type.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Stack>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setChannelDialogOpen(false)}></Button>
              <Button onClick={handleCreateChannel} variant="contained">
                
              </Button>
            </DialogActions>
          </Dialog>

          {/*  */}
          <Dialog open={!!editingCategory} onClose={() => setEditingCategory(null)}>
            <DialogTitle></DialogTitle>
            <DialogContent>
              <TextField
                autoFocus
                margin="dense"
                label=""
                fullWidth
                variant="outlined"
                value={editCategoryName}
                onChange={(e) => setEditCategoryName(e.target.value)}
              />
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setEditingCategory(null)}></Button>
              <Button onClick={handleEditCategory} variant="contained">
                
              </Button>
            </DialogActions>
          </Dialog>

          {/*  */}
          <Dialog open={!!editingChannel} onClose={() => setEditingChannel(null)} maxWidth="sm" fullWidth>
            <DialogTitle></DialogTitle>
            <DialogContent>
              <Stack spacing={2} sx={{ mt: 1 }}>
                <TextField
                  label=""
                  fullWidth
                  variant="outlined"
                  value={editChannelData.name}
                  onChange={(e) => setEditChannelData({ ...editChannelData, name: e.target.value })}
                  required
                />
                <TextField
                  label=""
                  fullWidth
                  variant="outlined"
                  multiline
                  rows={2}
                  value={editChannelData.description}
                  onChange={(e) => setEditChannelData({ ...editChannelData, description: e.target.value })}
                />
                <FormControl fullWidth required>
                  <InputLabel></InputLabel>
                  <Select
                    value={editChannelData.channelType}
                    onChange={(e) => setEditChannelData({ ...editChannelData, channelType: e.target.value })}
                  >
                    {channelTypes.map((type) => (
                      <MenuItem key={type.value} value={type.value}>
                        {type.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Stack>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setEditingChannel(null)}></Button>
              <Button onClick={handleEditChannel} variant="contained">
                
              </Button>
            </DialogActions>
          </Dialog>

          {/*  */}
          <Dialog open={roleDialogOpen} onClose={() => setRoleDialogOpen(false)}>
            <DialogTitle></DialogTitle>
            <DialogContent>
              <Typography variant="body2" sx={{ mb: 2 }}>
                {selectedUser?.username} 
              </Typography>
              <FormControl fullWidth>
                <InputLabel></InputLabel>
                <Select
                  value={newRole}
                  onChange={(e) => setNewRole(e.target.value)}
                >
                  {roles.map((role) => (
                    <MenuItem key={role} value={role}>
                      {role}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setRoleDialogOpen(false)}></Button>
              <Button onClick={handleChangeUserRole} variant="contained">
                
              </Button>
            </DialogActions>
          </Dialog>

          {/*  */}
          <Dialog open={deleteUserDialogOpen} onClose={() => setDeleteUserDialogOpen(false)}>
            <DialogTitle></DialogTitle>
            <DialogContent>
              <Typography variant="body2" sx={{ mb: 2 }}>
                {userToDelete?.username} 
              </Typography>
              <Typography variant="body2" color="error">
                
              </Typography>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setDeleteUserDialogOpen(false)}></Button>
              <Button onClick={handleDeleteUser} variant="contained" color="error">
                
              </Button>
            </DialogActions>
          </Dialog>
        </Box>
      </Fade>
    </Container>
  );
};

export default AdminPanel; 